import { useState, useEffect } from 'react';
import { tarotCards } from '../data/tarotCardsData';

/**
 * Custom hook ƒë·ªÉ qu·∫£n l√Ω tr·∫°ng th√°i tr·∫£i b√†i Tarot
 * 
 * @param {string} initialSpreadType - Lo·∫°i tr·∫£i b√†i m·∫∑c ƒë·ªãnh
 * @returns {Object} C√°c state v√† function ƒë·ªÉ qu·∫£n l√Ω tr·∫£i b√†i
 */
export const useTarotReading = (initialSpreadType = 'three-card') => {
  // Tr·∫°ng th√°i
  const [cards, setCards] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [spreadType, setSpreadType] = useState(initialSpreadType);
  const [question, setQuestion] = useState('');
  const [result, setResult] = useState(null);
  
  // Kh·ªüi t·∫°o spreadType t·ª´ prop initialSpreadType
  useEffect(() => {
    if (initialSpreadType && ['three-card', 'celtic-cross', 'love', 'career'].includes(initialSpreadType)) {
      setSpreadType(initialSpreadType);
    }
  }, [initialSpreadType]);
  
  // X√°o b√†i v√† ch·ªçn s·ªë l∆∞·ª£ng b√†i theo lo·∫°i tr·∫£i
  const shuffleAndDrawCards = () => {
    try {
      // X√°o tr·ªôn b√†i
      const shuffledCards = [...tarotCards].sort(() => Math.random() - 0.5);
      
      // X√°c ƒë·ªãnh s·ªë l√° b√†i c·∫ßn r√∫t theo lo·∫°i tr·∫£i
      let cardCount = 3; // M·∫∑c ƒë·ªãnh l√† 3 l√°
      
      switch (spreadType) {
        case 'three-card':
          cardCount = 3;
          break;
        case 'celtic-cross':
          cardCount = 10;
          break;
        case 'love':
        case 'career':
          cardCount = 6;
          break;
        default:
          cardCount = 3;
      }
      
      // R√∫t ƒë√∫ng s·ªë l√° b√†i c·∫ßn thi·∫øt
      const drawnCards = shuffledCards.slice(0, cardCount).map(card => ({
        ...card,
        isReversed: Math.random() > 0.7 // 30% kh·∫£ nƒÉng l√° b√†i b·ªã ng∆∞·ª£c
      }));
      
      return drawnCards;
    } catch (err) {
      console.error('Error shuffling and drawing cards:', err);
      throw new Error('Kh√¥ng th·ªÉ x√°o b√†i v√† r√∫t b√†i. Vui l√≤ng th·ª≠ l·∫°i.');
    }
  };
  
  // T·∫°o k·∫øt qu·∫£ ph√¢n t√≠ch b√†i t·ª´ c√°c l√° b√†i ƒë√£ r√∫t
  const generateReadingResult = (drawnCards, userQuestion) => {
    try {
      let resultText = '';
      
      // T·∫°o ph·∫ßn m·ªü ƒë·∫ßu
      if (userQuestion) {
        resultText += `D·ª±a tr√™n c√¢u h·ªèi c·ªßa b·∫°n: "${userQuestion}"\n\n`;
      }
      
      // Th√™m ph·∫ßn gi·ªõi thi·ªáu chung v·ªÅ tr·∫£i b√†i
      switch (spreadType) {
        case 'three-card':
          resultText += `Tr·∫£i b√†i Ba L√° (Qu√° kh·ª©, Hi·ªán t·∫°i, T∆∞∆°ng lai) cho th·∫•y:\n\n`;
          resultText += `Qu√° kh·ª© (${drawnCards[0]?.name}): ${drawnCards[0]?.isReversed ? drawnCards[0]?.reversedMeaning : drawnCards[0]?.meaning}\n\n`;
          resultText += `Hi·ªán t·∫°i (${drawnCards[1]?.name}): ${drawnCards[1]?.isReversed ? drawnCards[1]?.reversedMeaning : drawnCards[1]?.meaning}\n\n`;
          resultText += `T∆∞∆°ng lai (${drawnCards[2]?.name}): ${drawnCards[2]?.isReversed ? drawnCards[2]?.reversedMeaning : drawnCards[2]?.meaning}\n\n`;
          break;
          
        case 'celtic-cross':
          resultText += `Tr·∫£i b√†i Celtic Cross l√† m·ªôt tr·∫£i b√†i ph·ª©c t·∫°p v·ªõi 10 l√° b√†i, ph√¢n t√≠ch s√¢u v·ªÅ t√¨nh hu·ªëng c·ªßa b·∫°n:\n\n`;
          // Th√™m ph√¢n t√≠ch cho t·ª´ng v·ªã tr√≠ trong Celtic Cross
          resultText += `1. T√¨nh tr·∫°ng hi·ªán t·∫°i (${drawnCards[0]?.name}): ${drawnCards[0]?.isReversed ? drawnCards[0]?.reversedMeaning : drawnCards[0]?.meaning}\n\n`;
          resultText += `2. Th√°ch th·ª©c (${drawnCards[1]?.name}): ${drawnCards[1]?.isReversed ? drawnCards[1]?.reversedMeaning : drawnCards[1]?.meaning}\n\n`;
          // Th√™m c√°c v·ªã tr√≠ c√≤n l·∫°i...
          break;
          
        case 'love':
          resultText += `Tr·∫£i b√†i T√¨nh Y√™u cho th·∫•y:\n\n`;
          resultText += `B·∫°n (${drawnCards[0]?.name}): ${drawnCards[0]?.isReversed ? drawnCards[0]?.reversedMeaning : drawnCards[0]?.meaning}\n\n`;
          resultText += `ƒê·ªëi ph∆∞∆°ng (${drawnCards[1]?.name}): ${drawnCards[1]?.isReversed ? drawnCards[1]?.reversedMeaning : drawnCards[1]?.meaning}\n\n`;
          resultText += `M·ªëi quan h·ªá hi·ªán t·∫°i (${drawnCards[2]?.name}): ${drawnCards[2]?.isReversed ? drawnCards[2]?.reversedMeaning : drawnCards[2]?.meaning}\n\n`;
          // Th√™m c√°c v·ªã tr√≠ c√≤n l·∫°i...
          break;
          
        case 'career':
          resultText += `Tr·∫£i b√†i S·ª± Nghi·ªáp cho th·∫•y:\n\n`;
          resultText += `T√¨nh h√¨nh hi·ªán t·∫°i (${drawnCards[0]?.name}): ${drawnCards[0]?.isReversed ? drawnCards[0]?.reversedMeaning : drawnCards[0]?.meaning}\n\n`;
          resultText += `Th√°ch th·ª©c (${drawnCards[1]?.name}): ${drawnCards[1]?.isReversed ? drawnCards[1]?.reversedMeaning : drawnCards[1]?.meaning}\n\n`;
          // Th√™m c√°c v·ªã tr√≠ c√≤n l·∫°i...
          break;
          
        default:
          resultText += `C√°c l√° b√†i trong tr·∫£i b√†i c·ªßa b·∫°n:\n\n`;
          drawnCards.forEach((card, index) => {
            resultText += `L√° ${index + 1} (${card.name}): ${card.isReversed ? card.reversedMeaning : card.meaning}\n\n`;
          });
      }
      
      // Th√™m l·ªùi k·∫øt
      resultText += `T√≥m l·∫°i: Tr·∫£i b√†i n√†y g·ª£i √Ω r·∫±ng b·∫°n ƒëang tr·∫£i qua m·ªôt giai ƒëo·∫°n chuy·ªÉn ti·∫øp quan tr·ªçng. H√£y l·∫Øng nghe tr·ª±c gi√°c c·ªßa m√¨nh v√† suy ng·∫´m v·ªÅ nh·ªØng th√¥ng ƒëi·ªáp m√† c√°c l√° b√†i ƒë√£ mang ƒë·∫øn.`;
      
      return resultText;
    } catch (err) {
      console.error('Error generating reading result:', err);
      return 'Kh√¥ng th·ªÉ t·∫°o k·∫øt qu·∫£ tr·∫£i b√†i. Vui l√≤ng th·ª≠ l·∫°i.';
    }
  };
  
  // B·∫Øt ƒë·∫ßu tr·∫£i b√†i
  const startReading = async (formData) => {
    try {
      setIsLoading(true);
      setError(null);
      
      // C·∫≠p nh·∫≠t state t·ª´ formData
      if (formData.spreadType) {
        setSpreadType(formData.spreadType);
      }
      
      if (formData.question !== undefined) {
        setQuestion(formData.question);
      }
      
      // X√°o b√†i v√† r√∫t b√†i
      const drawnCards = shuffleAndDrawCards();
      
      // M√¥ ph·ªèng ƒë·ªô tr·ªÖ m·∫°ng
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // T·∫°o k·∫øt qu·∫£ ph√¢n t√≠ch
      const resultText = generateReadingResult(drawnCards, formData.question);
      
      // C·∫≠p nh·∫≠t state
      setCards(drawnCards);
      setResult(resultText);
      
    } catch (err) {
      console.error('Error starting reading:', err);
      setError(err.message || 'Kh√¥ng th·ªÉ tr·∫£i b√†i. Vui l√≤ng th·ª≠ l·∫°i.');
    } finally {
      setIsLoading(false);
    }
  };
  
  // Reset tr·∫£i b√†i
  const resetReading = () => {
    setCards([]);
    setResult(null);
    setError(null);
    // Gi·ªØ nguy√™n spreadType v√† question ƒë·ªÉ ti·ªán cho l·∫ßn tr·∫£i b√†i ti·∫øp theo
  };
  
  return {
    cards,
    isLoading,
    error,
    spreadType,
    question,
    result,
    startReading,
    resetReading
  };
}; 