import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';

/**
 * Component hi·ªÉn th·ªã m·ªôt l√° b√†i Tarot v·ªõi hi·ªáu ·ª©ng l·∫≠t b√†i 3D
 * 
 * @param {Object} props - Component props
 * @param {Object} props.card - D·ªØ li·ªáu c·ªßa l√° b√†i
 * @param {boolean} props.isFlipped - Tr·∫°ng th√°i l√° b√†i ƒë√£ l·∫≠t hay ch∆∞a
 * @param {Function} props.onClick - Callback khi click v√†o l√° b√†i
 * @param {boolean} props.isSelectable - L√° b√†i c√≥ ƒë∆∞·ª£c ch·ªçn kh√¥ng
 * @param {boolean} props.isSelected - L√° b√†i ƒë√£ ƒë∆∞·ª£c ch·ªçn ch∆∞a 
 * @param {number} props.selectedIndex - Th·ª© t·ª± c·ªßa l√° b√†i ƒë∆∞·ª£c ch·ªçn
 * @param {string} props.size - K√≠ch th∆∞·ªõc c·ªßa l√° b√†i ('small', 'medium', 'large')
 */
const TarotCard = ({
  card,
  isFlipped = false,
  onClick,
  isSelectable = false,
  isSelected = false,
  selectedIndex = null,
  size = 'medium'
}) => {
  const [isHovering, setIsHovering] = useState(false);
  const [cardFlipped, setCardFlipped] = useState(isFlipped);
  const [isReversal, setIsReversal] = useState(false);

  // Thi·∫øt l·∫≠p tr·∫°ng th√°i ng∆∞·ª£c khi component mount
  useEffect(() => {
    // Ki·ªÉm tra n·∫øu card t·ªìn t·∫°i v√† c√≥ thu·ªôc t√≠nh isReversed
    if (card && 'isReversed' in card) {
      setIsReversal(card.isReversed);
    } else {
      // Ng·∫´u nhi√™n ƒë·∫∑t l√° b√†i ng∆∞·ª£c ho·∫∑c kh√¥ng (20% kh·∫£ nƒÉng b·ªã ng∆∞·ª£c)
      setIsReversal(Math.random() < 0.2);
    }
  }, [card]);

  // Update cardFlipped khi prop isFlipped thay ƒë·ªïi
  useEffect(() => {
    setCardFlipped(isFlipped);
  }, [isFlipped]);

  // X·ª≠ l√Ω khi hover v√†o l√° b√†i
  const handleMouseEnter = () => {
    if (isSelectable && !isSelected) {
      setIsHovering(true);
    }
  };

  const handleMouseLeave = () => {
    setIsHovering(false);
  };

  // X·ª≠ l√Ω khi click v√†o l√° b√†i
  const handleClick = () => {
    if (onClick && (isSelectable || !isSelectable)) {
      onClick(card);
    }
  };

  // N·∫øu kh√¥ng c√≥ d·ªØ li·ªáu card, hi·ªÉn th·ªã card placeholder
  if (!card) {
    return (
      <div 
        className={`
          ${size === 'small' ? 'w-24 h-36' : size === 'medium' ? 'w-32 h-48' : 'w-40 h-60'}
          bg-gradient-to-br from-[#2a1045]/50 to-[#3a1c5a]/50 
          rounded-lg shadow-lg border border-purple-900/20
        `}
      >
        <div className="w-full h-full flex items-center justify-center">
          <span className="text-white opacity-50 text-xs">L√° b√†i kh√¥ng t·ªìn t·∫°i</span>
        </div>
      </div>
    );
  }

  // K√≠ch th∆∞·ªõc c·ªßa card
  const sizeClasses = {
    small: 'w-24 h-36',
    medium: 'w-32 h-48', 
    large: 'w-40 h-60'
  };

  return (
    <div 
      className={`
        ${sizeClasses[size]} 
        relative
        transition-transform duration-300
        ${isHovering ? 'scale-105' : ''}
        ${isSelected ? 'ring-4 ring-[#9370db] scale-105' : ''}
      `}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {/* Hi·ªáu ·ª©ng glow cho card khi hover ho·∫∑c selected */}
      {(isHovering || isSelected) && (
        <div className="absolute -inset-3 bg-gradient-to-r from-[#9370db] to-[#8a2be2] rounded-lg opacity-70 blur-md -z-10"></div>
      )}

      {/* Card container v·ªõi transform 3D */}
      <motion.div 
        className="relative w-full h-full"
        initial={{ rotateY: 0 }}
        animate={{ rotateY: cardFlipped ? 180 : 0 }}
        transition={{ duration: 0.8, ease: "easeInOut" }}
        style={{ 
          transformStyle: 'preserve-3d',
          perspective: '1000px'
        }}
      >
        {/* Card back (m·∫∑t √∫p) */}
        <div 
          className={`
            absolute w-full h-full rounded-lg shadow-lg overflow-hidden
            ${!cardFlipped ? 'border-2 border-[#9370db]/30' : ''}
          `}
          style={{ backfaceVisibility: 'hidden' }}
        >
          <div className="absolute inset-0 bg-gradient-to-br from-[#2a1045] to-[#3a1c5a]"></div>
          
          {/* Fallback if tarot back image not available */}
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="w-full h-full bg-[#2a1045] flex items-center justify-center">
              <span className="text-lg text-white/50">‚ô§ ‚ô° ‚ôß ‚ô¢</span>
            </div>
          </div>
          
          {/* Pattern overlay */}
          <div className="absolute inset-0 bg-opacity-20"></div>
          
          {/* Sparkle effect */}
          <div className="absolute inset-0 overflow-hidden">
            <div className="absolute h-[500%] w-[50%] top-[-200%] left-[-100%] bg-gradient-to-b from-transparent via-white to-transparent opacity-10 transform rotate-45"></div>
          </div>
        </div>

        {/* Card front (m·∫∑t ng·ª≠a) */}
        <div 
          className={`
            absolute w-full h-full rounded-lg shadow-lg overflow-hidden
            border-2 border-[#9370db]/30
            ${isReversal ? 'rotate-180' : ''}
          `}
          style={{ 
            backfaceVisibility: 'hidden',
            transform: 'rotateY(180deg)'
          }}
        >
          {card.imageUrl ? (
            <div className="w-full h-full relative">
              <img 
                src={card.imageUrl} 
                alt={card.name || 'Tarot Card'} 
                className="w-full h-full object-cover"
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src = "https://placehold.co/600x900/2a1045/9370db?text=Tarot";
                }}
              />
              
              {/* Vignette effect */}
              <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent"></div>
              
              {/* Card name */}
              <div className="absolute bottom-2 left-0 right-0 text-center">
                <p className="text-white text-sm font-medium drop-shadow-lg">
                  {card.name || 'Tarot Card'}
                </p>
              </div>
            </div>
          ) : (
            <div className="w-full h-full bg-gradient-to-br from-[#2a1045] to-[#3a1c5a] flex items-center justify-center">
              <p className="text-white text-center p-2">{card.name || 'Tarot Card'}</p>
            </div>
          )}
          
          {/* Selected number indicator */}
          {isSelected && selectedIndex !== null && (
            <div className="absolute top-2 right-2 bg-[#9370db] rounded-full w-6 h-6 flex items-center justify-center">
              <span className="text-white text-xs font-bold">{selectedIndex}</span>
            </div>
          )}
        </div>
      </motion.div>

      {/* Clickable overlay */}
      <div 
        className="absolute inset-0 z-10 cursor-pointer" 
        onClick={handleClick}
        aria-label={card.name || "Tarot Card"}
      ></div>
    </div>
  );
};

TarotCard.propTypes = {
  card: PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    name: PropTypes.string,
    imageUrl: PropTypes.string,
    isReversed: PropTypes.bool
  }),
  isFlipped: PropTypes.bool,
  onClick: PropTypes.func,
  isSelectable: PropTypes.bool,
  isSelected: PropTypes.bool,
  selectedIndex: PropTypes.number,
  size: PropTypes.oneOf(['small', 'medium', 'large'])
};

export default TarotCard; 